@import "config";
@import "home";
@import "menu";

body {
	background: $primary-color;
	color: set-text-color($primary-color);
	height: 100vh;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	line-height: 1;
}

h1,
h2,
h3 {
	font-weight: 400;
}

a {
	color: set-text-color($primary-color);
	text-decoration: none;
}

header {
	position: fixed;
	z-index: 2;
	width: 100%;
	padding: 1rem;
}

main {
	height: 100%;
	width: 100%;

	.social-icons {
		position: fixed;
		left: 1rem;
		bottom: 1rem;
		a {
			padding: 0.4rem;
			@include transition-ease;
			&:hover {
				color: $secondary-color;
			}
		}
	}
}

footer {
	position: fixed;
	bottom: 0.4rem;
	right: 1rem;
	text-align: right;
	padding: 1rem;
	color: set-text-color($primary-color);
}
